export const sendGcode = async function(gcode) {
  for (const line of gcode) {
    if (!line.startsWith(";") && line) {
      while (true) {
        if (this.cancel)
          break;
        if (this.ok) {
          console.log("Sending:", line);
          this.writer.write(line + "\n");
          this.ok = false;
          break;
        }
        await this.wait(10);
      }
    }
  }
};
